import { RouterModule, Routes } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { RedefinitionComponent } from '@app/project/redefinition/redefinition.component';


const redefinintionRoutes: Routes = [
    { path: '', component: RedefinitionComponent }
];

export const redefinitionRouting: ModuleWithProviders = RouterModule.forChild(redefinintionRoutes);



