import {
  Component,
  OnInit,
  ElementRef,
  AfterViewInit,
  ViewChild,
  Output,
  EventEmitter
} from '@angular/core';
import { DomCanvas, DomShape } from '@app/util/dom-canvas/dom-canvas.model';
import { DomCanvasEvents } from '@app/util/dom-canvas/dom-canvas-events';

@Component({
  selector: 'app-dom-canvas',
  templateUrl: './dom-canvas.component.html',
  styleUrls: ['./dom-canvas.component.css']
})
export class DomCanvasComponent
  implements OnInit, AfterViewInit, DomCanvasEvents {

  @ViewChild('domCanvas') protected canvas: ElementRef;

  @Output() public insertNode: EventEmitter<{ x: Number; y: Number }>;
  @Output() public nodeRemoved: EventEmitter<{ shape: any }>;

  public domCanvas: DomCanvas;

  constructor() {
    this.insertNode = new EventEmitter();
    this.nodeRemoved = new EventEmitter();
  }

  ngOnInit() {
    this.adjustCanvasBounds();
  }

  ngAfterViewInit(): void {
    this.domCanvas = new DomCanvas(this.canvas.nativeElement);
    this.domCanvas.eventsCallback = this;
  }

  adjustCanvasBounds() {
    const previousBounds = (this.canvas
      .nativeElement as HTMLCanvasElement).getBoundingClientRect();

    (this.canvas.nativeElement as HTMLCanvasElement).width =
      Math.round(devicePixelRatio * previousBounds.right) -
      Math.round(devicePixelRatio * previousBounds.left);
    (this.canvas.nativeElement as HTMLCanvasElement).height =
      Math.round(devicePixelRatio * previousBounds.bottom) -
      Math.round(devicePixelRatio * previousBounds.top);
  }

  addNewNode(xAxis: Number, yAxis: Number) {
    this.insertNode.emit({ x: xAxis, y: yAxis });
  }

  removeNode(shape: any) {
    this.nodeRemoved.emit(shape);
  }
}



