import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-color-selector',
  templateUrl: './color-selector.component.html',
  styleUrls: ['./color-selector.component.css']
})
export class ColorSelectorComponent implements OnInit {
  @Input() public backgroundColor = 'tomato';
  @Input() public isClicked = false;
  @Input() public textColor = 'white';
  @Output() public colorSelected: EventEmitter<String>;

  constructor() {
    this.colorSelected = new EventEmitter<String>();
  }

  ngOnInit() {
    this.isClicked = false;
  }

  public checkColor() {
    this.isClicked = !this.isClicked;

    if (this.isClicked) {
      this.colorSelected.emit(this.backgroundColor);
    }
  }
}



