import {
  Component,
  OnInit,
  AfterViewInit,
  ElementRef,
  ViewChild
} from '@angular/core';
import { DomCanvasComponent } from '@app/util/dom-canvas/dom-canvas.component';
import { DomShape } from '@app/util/dom-canvas/dom-canvas.model';
import { MatDialog } from '@angular/material';
import { NewNodeFormDialogComponent } from '@app/project/redefinition/mindmap/new-node-form-dialog/new-node-form-dialog.component';
import { MindmapService } from '@app/project/redefinition/mindmap/mindmap.service';
import { SessionService } from '@app/session.service';
import { MindMapModel } from '@app/model/mindmap.model';
import { NodeModel } from '@app/model/node.model';
import { DomSnackBarService } from '@app/util/dom-snack-bar/dom-snack-bar.service';

@Component({
  selector: 'app-mindmap',
  templateUrl: './mindmap.component.html',
  styleUrls: ['./mindmap.component.css']
})
export class MindmapComponent implements OnInit, AfterViewInit {
  @ViewChild('mindmap') protected mindMap: DomCanvasComponent;

  private mindMapModel: MindMapModel;
  public nodesToRemove: { nodeId: string }[];

  constructor(
    public dialog: MatDialog,
    public service: SessionService,
    public mindMapService: MindmapService,
    public snackBarService: DomSnackBarService
  ) {
    this.nodesToRemove = [];
  }

  ngOnInit() {}

  ngAfterViewInit(): void {
    this.mindMapModel = this.service.getSelectedProject().mindMap;
    if (this.mindMapModel.nodes && this.mindMapModel.nodes.length > 0) {
      this.mindMapModel.nodes.forEach(node => {
        node.getConnectedNodes(this.mindMapModel.nodes);
        this.mindMap.domCanvas.state.shapes.push(node.toDomShape());
      });

      this.mindMap.domCanvas.drawAll();
    }
  }

  public addNode() {
    console.log('Not implemented yet');
  }

  public clear() {
    this.mindMap.domCanvas.state.shapes.forEach(shape => {
      this.nodesToRemove.push({ nodeId: shape.id.toString() });
    });
    this.mindMap.domCanvas.clearAll();
  }

  public newNode(x, y, width, height, label, textColor, color = '#cccccc') {
    const shape = new DomShape().build(x, y, width, height, color, label);
    shape.textColor = textColor;

    if (this.mindMap.domCanvas.state.selection) {
      this.mindMap.domCanvas.state.selection.connections.push(shape);
      shape.connections.push(this.mindMap.domCanvas.state.selection);
      this.mindMap.domCanvas.linkShapes(
        this.mindMap.domCanvas.state.selection,
        shape,
        true
      );
    }

    this.mindMap.domCanvas.state.addShape(shape);
    this.mindMap.domCanvas.drawShape(shape);
  }

  public saveMindMap() {
    this.mindMapModel.nodes = [];
    this.mindMap.domCanvas.state.shapes.forEach(s => {
      const newShape = NodeModel.parseDomShape(s);
      newShape.idMindMap = this.mindMapModel.id;
      // newShape.setReferencedNodes();
      this.mindMapModel.nodes.push(newShape);
    });

    this.mindMapService
      .deleteUnusedNodes(this.mindMapModel, this.nodesToRemove)
      .subscribe(
        result => {
          this.mindMapService.updateMindMap(this.mindMapModel).subscribe(
            res => {
              this.snackBarService.snackBarSuccess('Mapa Mental Salvo!');
            },
            error => {
              this.snackBarService.snackBarError(
                'Erro ao salvar Mapa Mental - Cód 1'
              );
            }
          );
        },
        err => {
          this.snackBarService.snackBarError(
            'Erro ao salvar Mapa Mental - Cód 2'
          );
        }
      );
  }

  public clearMindMap() {
    this.mindMap.domCanvas.state.shapes.forEach(shape => {
      if (shape.id) {
        this.nodesToRemove.push({ nodeId: shape.id.toString() });
      }
    });
    this.mindMap.domCanvas.clearAll();
    this.snackBarService.snackBarInfo('Mapa Mental Limpo!');
  }

  public catchNodeRemoval(shape: any) {
    if (shape.id) {
      this.nodesToRemove.push({ nodeId: shape.id.toString() });
    }
  }

  public openAddNodeDialog(coordinates: { x; y }) {
    const dialogRef = this.dialog.open(NewNodeFormDialogComponent, {});

    dialogRef.afterClosed().subscribe(result => {
      // TODO
      if (result) {
        console.log(result);
        this.newNode(
          coordinates.x,
          coordinates.y,
          180,
          60,
          result.label,
          result.textColor,
          result.nodeColor
        );
      }
    });
  }
}



