import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MindmapComponent } from './mindmap.component';
import { MindmapNodeModule } from '@app/project/redefinition/mindmap/mindmap-node/mindmap-node.module';
import { DomCanvasModule } from '@app/util/dom-canvas/dom-canvas.module';
import { FlexLayoutModule } from '@angular/flex-layout';
import { MatButtonModule, MatSelectModule, MatDialogModule, MatIconModule, MatTooltipModule } from '@angular/material';
import { FormsModule } from '@angular/forms';
import { NewNodeFormDialogModule } from '@app/project/redefinition/mindmap/new-node-form-dialog/new-node-form-dialog.module';
import { DomApiModule } from '@app/util/dom-api/dom-api.module';
import { MindmapService } from '@app/project/redefinition/mindmap/mindmap.service';
import { DomSnackBarModule } from '@app/util/dom-snack-bar/dom-snack-bar.module';

@NgModule({
  imports: [
    CommonModule,
    DomApiModule,
    FlexLayoutModule,
    FormsModule,
    MatSelectModule,
    MatButtonModule,
    MatIconModule,
    MatTooltipModule,
    MindmapNodeModule,
    NewNodeFormDialogModule,
    DomSnackBarModule,
    MatDialogModule,
    DomCanvasModule
  ],
  providers: [MindmapService],
  declarations: [MindmapComponent],
  exports: [MindmapComponent]
})
export class MindmapModule { }



