import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { MatDialogRef } from '@angular/material';
import { NodeParams } from '@app/project/redefinition/mindmap/new-node-form-dialog/node-params';

@Component({
  selector: 'app-new-node-form-dialog',
  templateUrl: './new-node-form-dialog.component.html',
  styleUrls: ['./new-node-form-dialog.component.css']
})
export class NewNodeFormDialogComponent implements OnInit {
  constructor(public dialogRef: MatDialogRef<NewNodeFormDialogComponent>) {}

  public nodeLabel: String;
  public nodeColors;

  ngOnInit() {}

  public onColorSelectedListener(selection: { node: String; text: String }) {
    this.nodeColors = selection;
  }

  public createNodeClick() {
    if (this.nodeLabel && this.nodeColors) {
      this.dialogRef.close({
        label: this.nodeLabel,
        textColor: this.nodeColors.text,
        nodeColor: this.nodeColors.node
      });
    }
  }
}



