import { Injectable } from '@angular/core';
import { DomApiModule } from '@app/util/dom-api/dom-api.module';
import { DomApiService } from '@app/util/dom-api/dom-api.service';
import { ReadOnlyUtil } from '@app/util/read-only-util';
import { MindMapModel } from '@app/model/mindmap.model';
import { MindMapParser } from '@app/model/parser/mindmap.parser';

@Injectable()
export class MindmapService {
  constructor(protected domApi: DomApiService) {}

  public getMindMap(projectId: string) {
    return this.domApi.get(
      `${ReadOnlyUtil.RequestPaths.SERVER_ROOT}/${
        ReadOnlyUtil.RequestPaths.MINDMAP
      }/project/${projectId}`
    );
  }

  public deleteUnusedNodes(
    mindmap: MindMapModel,
    nodesToRemove: { nodeId: string }[] = []
  ) {
    const body = JSON.stringify(nodesToRemove);
    console.log(body);
    return this.domApi.post(
      `${ReadOnlyUtil.RequestPaths.SERVER_ROOT}/${
        ReadOnlyUtil.RequestPaths.MINDMAP
      }/delete-nodes/${mindmap.id}`,
      body
    );
  }

  public updateMindMap(mindmap: MindMapModel) {
    const body = JSON.stringify(MindMapParser.parseToJson(mindmap));

    return this.domApi.put(
      `${ReadOnlyUtil.RequestPaths.SERVER_ROOT}/${
        ReadOnlyUtil.RequestPaths.MINDMAP
      }/u/${mindmap.id}`,
      body
    );
  }
}



