import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { NewNodeFormDialogComponent } from './new-node-form-dialog.component';
import { MatDialogModule, MatButtonModule, MatInputModule, MatIconModule } from '@angular/material';
import { FlexLayoutModule } from '@angular/flex-layout';
import { FormsModule } from '@angular/forms';
import { SelectionColorGridModule } from '@app/util/selection-color-grid/selection-color-grid.module';

@NgModule({
  imports: [
    CommonModule,
    FlexLayoutModule,
    FormsModule,
    SelectionColorGridModule,
    MatInputModule,
    MatIconModule,
    MatDialogModule,
    MatButtonModule
  ],
  declarations: [NewNodeFormDialogComponent],
  exports: [NewNodeFormDialogComponent],
  entryComponents: [NewNodeFormDialogComponent]
})
export class NewNodeFormDialogModule { }



