import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { colorList } from '@app/util/selection-color-grid/colors';

interface Color {
  position: Number;
  color: String;
  textColor: String;
  isSelected: Boolean;
}

@Component({
  selector: 'app-selection-color-grid',
  templateUrl: './selection-color-grid.component.html',
  styleUrls: ['./selection-color-grid.component.css']
})
export class SelectionColorGridComponent implements OnInit {
  @Output() public colorSelection: EventEmitter<{ node: String; text: String }>;

  public colors: Color[];
  public selectedColor: Color;

  constructor() {
    this.colors = [];
    this.selectedColor = null;
    this.colorSelection = new EventEmitter();
  }

  ngOnInit() {
    this.fillColors();
  }

  public onColorSelectedListener(color: String) {
    if (this.selectedColor) {
      this.selectedColor.isSelected = false;
      // this.selectedColor = null;
    }

    this.selectedColor = this.colors.find(c => {
      return c.color === color;
    });

    this.selectedColor.isSelected = true;

    this.colorSelection.emit({
      node: this.selectedColor.color,
      text: this.selectedColor.textColor
    });
  }

  private getIndex(colorToSearch) {
    return this.colors.findIndex(c => {
      return c.color === colorToSearch;
    });
  }

  private fillColors() {
    this.colors = colorList;
  }
}



