import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RedefinitionComponent } from './redefinition.component';
import { MindmapModule } from '@app/project/redefinition/mindmap/mindmap.module';
import { redefinitionRouting } from '@app/project/redefinition/redefinition.routing';
import { DomFlexContainerModule } from '@app/util/dom-flex-container/dom-flex-container.module';

@NgModule({
  imports: [
    CommonModule,
    MindmapModule,
    DomFlexContainerModule,
    redefinitionRouting
  ],
  declarations: [RedefinitionComponent]
})
export class RedefinitionModule { }



